.form{
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    padding: 10px;
}
.container {
    background-color: #fff;
    display: inline-block;
    margin-left: 0 auto;
    outline: 2px solid  #525151;
    padding: 10px;
    width: 400px;
    border-radius: 3px;
    background-color: #fff;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
  }
  .container:hover {
    outline: 2px solid rgb(251, 195, 76);
  }
  .label {
    font-family: Roboto;
    font-size: 15px;
    line-height: 13px;
    letter-spacing: 0.03em;
    color: #757575;
    margin-top: 10px;
  }
  
  .input {
    margin-left: 10px;
    padding: 5px;
    border: none;
    border-radius: 5px;
    background-color: #ccc;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    outline: 1px solid rgba(110, 167, 18, 0.322);
  }
  input:focus {
    outline: 3px solid rgba(232, 139, 33, 0.264);;
  }
  .button {
    display: block;
    font-family: Roboto;
    font-size: 15px;
    line-height: 13px;
    letter-spacing: 0.03em;
    margin-top: 10px;
    width: 100px;
    height: auto;
    padding: 5px;
    color: #525151;
    border-radius: 5px;
    border: none;
    background-color: rgba(232, 139, 33, 0.264);
    outline: 2px solid rgb(251, 195, 76);
    cursor: pointer;
  }
  .button:hover {
    background-color: rgba(232, 139, 33, 0.264);
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
      0px 2px 1px rgba(0, 0, 0, 0.2);
    outline: none;
  }
  